<template>
	<view class="jilupage"> 
		<!-- <view class="jilupage0">
		 	<text class="jilupage1">支出￥20元</text>
		 	<text>收入￥20元</text>
		 </view> -->
		 <view class="kefu" v-if="lists.length>0" @click="copys('wo9999vip')">如有充值异常请联系客服（点击复制）</view>
		 <view class="jilupageList" v-for="(item,index) in lists" :key="index">
		    <view class="jilupageList0">
				<view class="jilupageList01">
					<image :src="shouji" class="jilupageImg" mode=""></image>
					<view class="">
						<view class="jilupageList02"> 手机充值</view>
						<view class="jilupageList03"> 充值缴费{{item.orderAmount/100}}</view>
						<view class="jilupageList03"> 今天{{item.createTime}}</view>
					</view>
				</view>
				<view class="">
					<view class="jilupageList02"> {{item.rechargeAmount/100}}</view>
					<view class="jilupageList03">
					<text v-if="item.payState==0">未支付</text>	 
					<text v-if="item.payState==1">处理中</text>	 
					<text v-if="item.payState==2">成功</text>	 
					<text v-if="item.payState==3">失败 </text>	 
					<text v-if="item.payState==4">支付异常</text>	 
					</view>
					<view class="jilupageList03">
					<text v-if="item.rechargeState==0">未充值</text>	 
					<text v-if="item.rechargeState==1">处理中</text>	 
					<text v-if="item.rechargeState==2">成功</text>	 
					<text v-if="item.rechargeState==3">失败 </text>	 
					<text v-if="item.rechargeState==4">支付异常</text>	 
					</view>
					 
				</view>
		    	 
		    </view>
		 </view>

	</view>
</template>

<script>
	var _this;
	import {
		chargelogs
	} from "@/api/index.js"
	export default {
		name: 'index',
	
		data() {
			return {
				lists:[],
	           shouji:require('../../static/images/sj.png')
			}
		},
		onLoad() {
			_this =this
			_this.getchargelogs()
		},
		onShow() {
			
		},
		methods:{
			/*
			            复制按钮的实现
			            */
			            copys(value) {
			                //提示模板
			                uni.showModal({
			                    content: value, //模板中提示的内容
			                    confirmText: '复制微信',
			                    success: () => { //点击复制内容的后调函数
			                        //uni.setClipboardData方法就是讲内容复制到粘贴板
			                        // API `setClipboardData` is not yet implemented
			                        //意思是H5端没有这个接口！！！
			                        uni.setClipboardData({
			                            data: value, //要被复制的内容
			                            success: function() {
			                                    //重点~做笔记
			                                    //在success中加入uni.hideToast()可以解决
			                                    uni.hideToast({
			                                        title: '复制成功',
			                                        duration: 2000,
			                                        icon: 'none'
			                                    });
			                                    //以下就可自定义操作了~
			                                },
			                                fail: function(err) {
			                                    uni.showToast({
			                                        title: '复制失败',
			                                        duration: 2000,
			                                        icon: 'none'
			                                    });
			                                }
			                        });
			                    }
			                });
			            },
			getchargelogs(){
				
				chargelogs({
					uid:uni.getStorageSync('uid') 
				}).then(response => {
					_this.lists = response.data
					console.log('99999',response.data)
				});
			}
			 
		}
		}
</script>

